  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background: #fff;
  border: 1px solid #408f95;
  width: 1.375rem;
  height: 1.375rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
