/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}

body {
  font-family: $base-font-family;
  color: $black;
  font-weight: $normal;
}
